import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import feather from 'vue-icon'
import io from 'socket.io-client'
const socket = io('https://back-wishpool.azurewebsites.net:3001'); //  websocket 

Vue.config.productionTip = false

Vue.prototype.$http = axios; //axios  
Vue.prototype.$socket = socket; //  
Vue.prototype.$url = 'http://localhost:8080'; // webpack server 
Vue.prototype.$api = 'https://back-wishpool.azurewebsites.net:3000';  // REST API

Vue.use(feather, 'v-icon');

// ,  socket id ,  uid .
socket.on('sid', sid=>{
  store.dispatch('updateSid', sid);
});

store.dispatch('checkLogin');

//    .
router.beforeEach(async (to, from, next)=>{
  //    
  if(to.matched.some(record => record.meta.requiresAuth)){
    //   .
    if(store.getters.isLoggedIn){
        next();
      }else{
        next({name:'login'});
      }
      if(store.state.status == 'error'){
        next({name:'login'})
      }
  }else{ //     .
    next(); 
  }
});

const EventBus = new Vue();
Vue.prototype.$bus = EventBus;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
